import { Button, Col, Form, Input, Modal, notification, Row } from 'antd';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import actions from '../../../redux/transaction/actions';

export default function ModalConfirmChangeQty({ open, state, onHandleCancel, handleCancel }) {
	const dispatch = useDispatch();
	const history = useHistory();

	const [form] = Form.useForm();

	const { isGrantedAccess = false, loading = false } = useSelector((state) => state?.auth);
	const { selectedRowTransaction = {}, data = [] } = useSelector((state) => state?.transaction);
	const { qty: oldQty, price = 0, ItemCode = '' } = selectedRowTransaction;
	const { qty = 0 } = state;

	const onFinish = (values) => {
		dispatch({
			type: 'auth/USER_ACCESS',
			payload: {
				email: values?.email,
				password: window.btoa(values?.password),
				funcId: '1031'
			},
			history
		});
	};

	useEffect(() => {
		dispatch({
			type: 'auth/SET_STATE',
			payload: {
				isGrantedAccess: false
			}
		});

		form.resetFields();
	}, [open]);

	useEffect(() => {
		if (open) {
			if (isGrantedAccess) {
				const restData = [];

				data?.map((el) => {
					if (el.ItemCode === ItemCode) {
						restData.push({
							...el,
							qty: +qty,
							total: +qty * +price,
							amount: +qty * +price
						});
					} else {
						return restData.push(el);
					}

					return true;
				});

				dispatch({
					type: actions.SET_STATE,
					payload: {
						data: [...restData]
					}
				});

				onHandleCancel();

				notification.success({
					message: 'Berhasil!',
					description: `Quantity berhasil diubah menjadi ${qty}.`
				});
				return;
			}
		}
	}, [isGrantedAccess, open]);

	return (
		<Modal centered open={open} onCancel={handleCancel} title={<h1 className="text-lg">Void Quantity</h1>} footer={false}>
			<Form
				form={form}
				name="login"
				layout="vertical"
				initialValues={{
					remember: true
				}}
				onFinish={onFinish}
				onFinishFailed={console.log}
				autoComplete="off"
				style={{ maxWidth: 'none', width: '100%' }}>
				<Row wrap gutter={[15, 10]}>
					<Col span={8}>
						<div className="p-3 bg-gray-200 rounded-md mb-5">
							Qty Awal : <b>{oldQty || 0}</b>
						</div>
					</Col>
					<Col span={8}>
						<div className="p-3 bg-gray-200 rounded-md mb-5">
							Qty Akhir : <b>{qty || 0}</b>
						</div>
					</Col>
					<Col span={8}>
						<div className="p-3 bg-gray-200 rounded-md mb-5">
							Qty Void : <b>{+oldQty - +qty || 0}</b>
						</div>
					</Col>
					<Col span={24}>
						<Form.Item
							label="Email"
							name="email"
							rules={[
								{
									required: true,
									message: 'Wajib Diisi!'
								}
							]}>
							<Input type="email" autoFocus autoComplete="false" placeholder="usertest@domain.com" className="w-full h-12 rounded-md" />
						</Form.Item>
					</Col>
					<Col span={24}>
						<Form.Item
							label="Password"
							name="password"
							rules={[
								{
									required: true,
									message: 'Wajib Diisi!'
								}
							]}>
							<Input.Password type="password" autoComplete="false" placeholder="********" className="w-full h-12 rounded-md" />
						</Form.Item>
					</Col>

					<Col span={24}>
						<div className="w-full flex items-center justify-start gap-2 mt-5">
							<Form.Item>
								<Button disable={loading} loading={loading} type="primary" htmlType="submit" className="w-full btn-page px-7 h-12">
									Konfirmasi
								</Button>
							</Form.Item>
							<Form.Item>
								<Button
									type="default"
									htmlType="button"
									className="w-full h-14 flex items-center justify-center gap-2 rounded-md px-7"
									onClick={handleCancel}>
									Kembali
								</Button>
							</Form.Item>
						</div>
					</Col>
				</Row>
			</Form>
		</Modal>
	);
}
