import { Button, Col, Input, Modal, notification, Row } from 'antd';
import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import actions from '../../../redux/transaction/actions';
import { currencyFormat } from '../../../utils/helper/utilities';
import ModalConfirmChangeQty from './ModalConfirmChangeQty';

export default function ModalChangeQty({ open, handleCancel, onHandleState }) {
	const dispatch = useDispatch();

	const { selectedRowTransaction = {}, data = [] } = useSelector((state) => state?.transaction);
	const { qty: oldQty, ItemCode = '', price = 0, upcBarcode = '', itemName = '' } = selectedRowTransaction;

	const inputRef = useRef(null);
	const modalRef = useRef(null);

	const [state, setState] = useState({
		qty: oldQty,
		inputChanged: false,
		modalConfirm: false
	});

	const onHandleCancel = () => {
		handleCancel();
		setState({
			qty: oldQty,
			inputChanged: false,
			modalConfirm: false
		});
	};

	const handleFocus = () => {
		const input = inputRef && inputRef.current !== null;

		if (input) {
			inputRef.current.focus();
		}
	};

	const handleFocusSelect = () => {
		const input = inputRef && inputRef.current !== null;

		if (input) {
			inputRef.current.focus();
			inputRef.current.select();
		}
	};

	const handleChange = (value) => {
		setState((prev) => ({
			...prev,
			qty: +value
		}));

		handleFocus();
	};

	const handleEnter = () => {
		const { qty = 0 } = state;
		handleFocus();

		if (+qty === 0 || +qty <= 0) {
			notification.error({
				message: 'Perhatian!',
				description: 'Kuantitas tidak boleh kosong'
			});

			handleFocus();
			return;
		}

		if (qty === '' || qty === oldQty) {
			handleChangeQty(+oldQty);
			return;
		}

		handleChangeQty(qty);
	};

	const handleChangeQty = (qty = oldQty) => {
		const anyData = data?.length !== 0 && data?.filter((e) => e.ItemCode === ItemCode);

		if (anyData && qty <= 0) {
			notification.info({
				message: 'Perhatian!',
				description: 'QTY Tidak boleh Nol (0)'
			});

			handleFocusSelect();
			return;
		}

		if (anyData && qty < oldQty) {
			handleState('modalConfirm', true);
			return;
		}

		if (anyData && qty >= 1) {
			const restData = [];

			data?.map((el) => {
				if (el.ItemCode === ItemCode) {
					restData.push({
						...el,
						qty: +qty,
						total: +qty * +price,
						amount: +qty * +price
					});
				} else {
					return restData.push(el);
				}

				return true;
			});

			dispatch({
				type: actions.SET_STATE,
				payload: {
					data: [...restData]
				}
			});

			setState((prev) => ({
				...prev,
				qty
			}));

			onHandleCancel();
			return;
		}
	};

	const handleKeyDownGlobal = (e) => {
		const allowedKeys = ['ArrowUp', 'ArrowRight', '1', '2', '3', '4', '5', '6', '7', '8', '9', '0'];
		const key = e.key;

		if (allowedKeys.includes(key)) {
			if (key === 'ArrowUp') {
				handleFocusSelect();
				return;
			}

			handleFocus();
		}
	};

	const handleState = (state, val) => {
		setState((prev) => ({
			...prev,
			[state]: val
		}));
	};

	useEffect(() => {
		if (open && !state?.modalConfirm) {
			handleFocus();
			document.addEventListener('keydown', handleKeyDownGlobal);
		}

		return () => {
			document.removeEventListener('keydown', handleKeyDownGlobal);
		};
	}, [open, state]);

	useEffect(() => {
		handleFocusSelect();
	}, [open]);

	useEffect(() => {
		setState((prev) => ({
			...prev,
			qty: oldQty
		}));
	}, [selectedRowTransaction]);

	return (
		<>
			<Modal
				centered
				open={open}
				onOk={handleEnter}
				onCancel={onHandleCancel}
				title={<p className="text-lg">Ubah Quantity</p>}
				footer={
					<div className="w-full h-auto flex items-center justify-end gap-1">
						<Button
							type="dashed"
							size="large"
							className="rounded-md text-sm"
							danger
							onClick={() => {
								onHandleState('modalConfirmDelete', true);
								onHandleState('modalQty', false);
							}}>
							Hapus Barang
						</Button>
						<Button
							onClick={() => {
								handleChangeQty(state?.qty);
							}}
							type="primary"
							size="large"
							className="rounded-md text-sm font-semibold">
							Ubah Quantity
						</Button>
					</div>
				}>
				<Row ref={modalRef} tabIndex={1} onKeyDown={handleKeyDownGlobal} wrap gutter={[8, 8]}>
					<Col span={24}>
						<Row>
							<Col span={10}>Barcode / PLU</Col>
							<Col span={1}>:</Col>
							<Col span={11}>{`${upcBarcode} / ${ItemCode}`}</Col>
						</Row>
					</Col>
					<Col span={24}>
						<Row>
							<Col span={10}>Nama Barang</Col>
							<Col span={1}>:</Col>
							<Col span={11}>{itemName}</Col>
						</Row>
					</Col>
					<Col span={24}>
						<Row>
							<Col span={10}>Harga Satuan</Col>
							<Col span={1}>:</Col>
							<Col span={11}>{currencyFormat(price, 0)}</Col>
						</Row>
					</Col>
					<Col span={24}>
						<Row>
							<Col span={10}>Qty</Col>
							<Col span={1}>:</Col>
							<Col span={11}>{oldQty}</Col>
						</Row>
					</Col>
					<Col span={24}>
						<Input
							autoFocus
							ref={inputRef}
							id="input-qty"
							value={state?.qty}
							defaultValue={oldQty}
							disabled={state.inputChanged}
							onChange={(event) => handleChange(event.target.value)}
							onPressEnter={() => {
								handleEnter();
							}}
							className="w-full h-14 rounded-lg mt-5 bg-gray-200"
							placeholder="MASUKKAN JUMLAH QTY"
							onFocus={(e) => {
								handleFocusSelect();
							}}
							onClick={() => {
								handleFocusSelect();
							}}
						/>

						<p className="text-sm text-red-600 mt-2">
							Gunakan <b>&uarr;</b> untuk memasukkan jumlah qty dan tekan <b>ENTER</b> untuk konfirmasi
						</p>
					</Col>
				</Row>
			</Modal>
			<ModalConfirmChangeQty
				open={state?.modalConfirm}
				state={state}
				handleCancel={() => handleState('modalConfirm', false)}
				handleState={handleState}
				onHandleCancel={onHandleCancel}
			/>
		</>
	);
}
