import { Form, Input, Modal, notification } from 'antd';
import React, { useEffect, useRef } from 'react';
import { BiSearch } from 'react-icons/bi';
import { CgSpinner } from 'react-icons/cg';
import { useDispatch, useSelector } from 'react-redux';
import useCartOrder from '../hooks/useCartOrder';
import TableSearchItem from './TableSearchItem';

export default function ModalTableSearchItem({ open = false, handleFocus, handleCancel }) {
	const dispatch = useDispatch();
	const inputRef = useRef(null);
	const modalRef = useRef(null);

	const { loading = false, searchResult = [] } = useSelector((state) => state?.transaction);
	const { inputRef: searchInputRef, form } = useCartOrder();

	const handleSearch = (query = '') => {
		if (query !== '') {
			dispatch({
				type: 'transaction/SEARCH_MASTER',
				payload: {
					query
				}
			});

			form.resetFields();
			focusInput();
			return;
		}

		focusInput();
		notification.warning({
			message: 'Perhatian!',
			description: 'Masukkan Kode Item / Kode Barcode terlebih dahulu.'
		});
	};

	const focusInput = () => {
		searchInputRef && searchInputRef.current !== null && searchInputRef.current.blur();
		if (inputRef && inputRef.current !== null) {
			inputRef.current.focus();
		}
	};

	const onKeyDown = (e) => {
		searchInputRef && searchInputRef.current !== null && searchInputRef.current.blur();

		if (e.key === 'F3') {
			e.preventDefault();
			focusInput();
		}

		if (e.key === 'Escape' || e.key === 'Tab') {
			handleCancel();
		}
	};

	useEffect(() => {
		if (open) {
			focusInput();
			form.resetFields();
		}

		document.addEventListener('keydown', (e) => {
			onKeyDown(e);
		});
		return () => {
			document.removeEventListener('keydown', onKeyDown);
		};
	}, [open]);

	useEffect(() => {
		focusInput();
	}, [searchResult]);

	useEffect(() => {
		focusInput();
	}, [loading]);

	return (
		<Modal open={open} onCancel={handleCancel} footer={false} centered width={1000} title="Pencarian Barang">
			<div ref={modalRef} onKeyDown={onKeyDown} className="w-full h-auto" id="modal-container">
				<div>
					<Form form={form}>
						<Form.Item name="search">
							<Input
								autoFocus
								ref={inputRef}
								// value={state?.barcode}
								type="text"
								className="w-full h-14 rounded-lg border-2 border-gray-400"
								placeholder="MASUKKAN PLU / BARCODE ITEM / NAMA ITEM (F3)"
								// onChange={(event) => {
								// 	handleState('barcode', event.target.value);
								// }}
								onPressEnter={(event) => {
									handleSearch(event.target.value);
									form.resetFields();
								}}
								suffix={
									loading ? (
										<CgSpinner className="animate-spin text-xl text-gray-500" />
									) : (
										<BiSearch className="text-xl text-gray-500" />
									)
								}
							/>
						</Form.Item>
					</Form>
				</div>
				<div className="w-full mt-4">
					<TableSearchItem modalRef={modalRef} handdleFocusInput={focusInput} handleCancel={handleCancel} />
				</div>
				<div className="w-full mt-4 rounded-md bg-gray-300 p-3">
					<h1 className="text-base">
						Tekan <b>F3</b> untuk mencari barang by plu/barcode barang. <b>F4</b> untuk memilih tabel, kemudian <b>↑ / ↓</b> untuk pilih
						barang dari tabel, tekan <b>Enter</b> untuk konfirmasi item/barang pilihan
					</h1>
				</div>
			</div>
		</Modal>
	);
}
